          used with all elements that spread 
          across the entire app.
**/
/**
  * @name animations
  * @desc Used for setting animations and animation
          properties used with all elements that 
          spread across the entire app.
**/
@keyframes animation-fade-in {
  from {
    opacity: 0;
  }
