/**
  * @name link
  * @desc Styles for the link module.
**/
.link {
  display: inline-block;
  position: relative;
}

.link span {
  transition: color 0.3s ease;
  transition-duration: 0.2s;
  display: inline-block;
  position: relative;
  z-index: 0;
  padding-bottom: 0.46em;
  color: white;
}

.link span:before {
  transition: width 0.3s ease, visibility 0.3s ease;
  transition-duration: 0.2s;
  position: absolute;
  visibility: hidden;
  content: "";
  z-index: -1;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: white;
}

.link:hover span {
  transition: color 0.3s ease;
  transition-duration: 0.2s;
}

.link:hover span:before {
  transition: width 0.3s ease, visibility 0.3s ease;
  transition-duration: 0.2s;
  visibility: visible;
  width: 100%;
}

.ulink, .news-tile__content a, .about-tile__content a {
  display: inline-block;
  position: relative;
}

.ulink span, .news-tile__content a span, .about-tile__content a span {
  display: inline;
  position: relative;
  padding-bottom: 0.46em;
  color: white;
  border-bottom-style: solid;
  border-bottom-color: white;
  border-bottom-width: 2px;
  /* line-height: $line_height_p + ($link_padding_vert * 1.25); */
}

.article-tile[data-style="black"] .link span, .article-tile[data-style="grey"] .link span, .link--white span {
  color: white;
}

.article-tile[data-style="black"] .link span:before, .article-tile[data-style="grey"] .link span:before, .link--white span:before {
  background-color: white;
}

.article-tile[data-style="yellow"] .link span, .link--black span {
